*, :before, :after {
  box-sizing: border-box;
}

.body, h2, p {
  margin: 0;
  padding: 0;
}

.people {
  z-index: 0;
  grid-template-columns: repeat(auto-fit, minmax(220px, 220px));
  justify-content: center;
  column-gap: 30px;
  display: grid;
  position: relative;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

.person {
  z-index: 1;
  cursor: pointer;
  will-change: transform;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 220px;
  padding: 12px 12px 19px;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.person__photo {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.person__photo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 40%;
  right: 0;
  transform: translate(-50%);
}

.person__name {
  color: #2f4f4f;
  margin-bottom: 9px;
  font-size: 20px;
  font-weight: 700;
}

.person__sport {
  opacity: 0;
  color: #000;
  text-align: center;
  height: 24px;
  font-size: 18px;
  font-weight: 400;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(10px);
}

.person:hover {
  z-index: 10;
  background-color: #eee;
  transform: scale(1.3);
}

.person:hover .person__photo {
  border-radius: 60px;
}

.person:hover .person__sport {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
